.header {
  background: var(--petrol-100);
  top: 0;
  border: 0;
  justify-content: center;
  height: 80px;
  display: flex;
  align-items: center;
  @media screen and (max-width: 1200px) {
    border-bottom: 1px solid var(--petrol-80);
  }
  @media screen and (max-width: 480px) {
    height: 70px;
  }
}
